<template>
  <div class="wrapper pa-4">
    <h1>0宽字符过滤器</h1>
    <p>常见的0宽字符:</p>
    <highlightjs
      :code="zeroWidthCharacters"
      lang="js"
    />
    <div class="d-flex mt-2">
      <p>输入length:{{ text.length }}</p>
      <VSpacer />
      <v-btn @click="add">
        添加0宽字符的段落
      </v-btn>
      <v-btn @click="handleClick">
        清除0宽字符
      </v-btn>
    </div>
    <textarea
      ref="textarea"
      v-model="text"
      class="pa-2"
      style="width: 100%;border: 1px solid lightblue;"
      rows="10"
    />
    <p>输出length:{{ outtext.length }}</p>
    <textarea
      ref="textarea"
      v-model="outtext"
      class="pa-2"
      style="width: 100%;border: 1px solid lightblue;"
      rows="10"
    />
  </div>
</template>

<script>

// ???
function removeZeroWidthCharacters(text) {
  return text.replace(/[\u200B-\u200D\uFEFF]/g, '');
}

export default {
  data() {
    return {
      zeroWidthCharacters: `\\u200B // zero-width space
\\u200C // zero-width non-joiner
\\u200D // zero-width joiner
\\uFEFF // zero-width no-break space
\\u00AD // soft hyphen
\\u2060 // word joiner`,
      text: 'A\u200CB\u200DC',
      outtext: '',
    }
  },

  methods: {
    handleClick() {
      this.outtext = removeZeroWidthCharacters(this.text);
      console.log(this.outtext === this.text);
    },
    add() {
      this.text += 'Lorem​Ipsum​Dolor​Sit​Amet​Consectetur​Adipiscing​Elit​Sed​Do​Eiusmod​Tempor​Incididunt​Ut​Labore​Et​Dolore​Magna​Aliqua​Ut​Enim​Ad​Minim​Veniam​Quis​Nostrud​Exercitation​Ullamco​Laboris​Nisi​Ut​Aliquip​Ex​Ea​Commodo​Consequat​Duis​Aute​Irure​Dolor​In​Reprehenderit​In​Voluptate​Velit​Esse​Cillum​Dolore​Eu​Fugiat​Nulla​Pariatur​Excepteur​Sint​Occaecat​Cupidatat​Non​Proident​Sunt​In​Culpa​Qui​Officia​Deserunt​Mollit​Anim​Id​Est​Laborum';
    },
  },
}
</script>

<style scoped>
.wrapper {
  display: grid;
  grid-template-columns:
    1fr
    min(65ch, 100%)
    1fr;
}

.wrapper > * {
  grid-column: 2;
}

.full-bleed {
  width: 100%;
  grid-column: 1 / -1;
}

</style>